
/* frontend/src/App.css */

/* The wrapper for the entire page */
.app-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* so footer can stick to bottom if content is short */
  background: #fafafa;
  font-family: sans-serif;
}

/* Gradient header */
.app-header {
  background: linear-gradient(90deg, #d54c4c 0%, #d42d2d 50%, #a02b2b 100%);
  color: #fff;
  padding: 1.5rem 1rem;
  text-align: center;
  box-shadow: 0 2px 6px rgba(0,0,0,0.2);
}
.header-title {
  margin: 0;
  font-size: 1.8rem;
}

/* The main container in the middle */
.app-container {
  max-width: 700px;
  width: 100%;
  margin: 2rem auto;
  background: #ffffff;
  padding: 1.5rem;
  border-radius: 8px;
  box-shadow: 0 0 8px rgba(0,0,0,0.1);
}

/* Input group layout */
.input-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
}

/* Label styling */
.label {
  font-weight: 600;
  margin-bottom: 0.3rem;
}

/* Input field styling */
.input {
  padding: 0.5rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

/* The search button */
.search-button {
  display: inline-block;
  padding: 0.6rem 1.2rem;
  background: #bc3939;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 1rem;
}
.search-button:hover {
  background: #e06666;
}

/* Error message in red */
.error-message {
  color: #cc0000;
  font-weight: 600;
  margin-bottom: 1rem;
}

/* Results container styling */
.results-container {
  margin-top: 2rem;
  background: #ffffff;
  padding: 1rem;
  border-radius: 4px;
  border: 1px solid #eee;
}

/* Table styling */
.store-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 2rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}
.store-table thead tr {
  background-color: #b73f3f;
  color: #fff;
}
.store-table th,
.store-table td {
  border: 1px solid #ccc;
  padding: 12px;
  text-align: left;
}
.store-table th {
  font-weight: 600;
}
.store-table tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}
.store-table tbody tr:hover {
  background-color: #eefbee;
}

/* Chart container */
.chart-container {
  margin-top: 1rem;
}
.chart-row {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
}
.chart-label {
  width: 150px;
  font-weight: 600;
}
.chart-bar {
  height: 20px;
  background: #db4e4e;
  color: #fff;
  text-align: right;
  padding-right: 5px;
  border-radius: 4px;
  line-height: 20px;
  /* Animate the bar width changes */
  transition: width 0.5s ease;
}

/* Fallback raw JSON container */
.raw-json-container {
  margin-top: 2rem;
  background: #ffffff;
  padding: 1rem;
  border-radius: 4px;
  border: 1px solid #eee;
}
.raw-json {
  background: #f0f0f0;
  padding: 1rem;
  white-space: pre-wrap;
  overflow-x: auto;
  border-radius: 4px;
}

/* Footer styling */
.app-footer {
  background: #aa2424;
  color: #fff;
  text-align: center;
  padding: 1rem;
  margin-top: auto; /* push footer to bottom if content is short */
}
.app-footer p {
  margin: 0;
  font-size: 0.9rem;
}

